webpackJsonp([40430315145424],{

/***/ 439:
/***/ (function(module, exports) {

	module.exports = {"pathContext":{"postsByTag":[{"excerpt":"이 글의 원문은  Simone Carletti가 작성하였습니다. 오역 지적 및 피드백은 더 나은 번역을 위한 밑거름이 됩니다. 저는 제 자신을 활발한  StackOverflow  사용자라고 생각합니다. 사이트 활동이 하루의 업무량에 크게 좌지우지되긴 하지만요. 2009년에 StackOverflow에 가입하여 이 글을 쓰는 시점(2016년 12월)까지,  저는 12만 4천의 명성치를 얻었고  상위 30…","html":"<ul>\n<li><strong>이 글의 원문은 <a href=\"https://simonecarletti.com/blog/2016/12/how-i-use-stackoverflow/\">Simone Carletti가 작성하였습니다.</a></strong></li>\n<li><strong>오역 지적 및 피드백은 더 나은 번역을 위한 밑거름이 됩니다.</strong></li>\n</ul>\n<hr>\n<p>저는 제 자신을 활발한 <a href=\"https://stackoverflow.com/\">StackOverflow</a> 사용자라고 생각합니다. 사이트 활동이 하루의 업무량에 크게 좌지우지되긴 하지만요. 2009년에 StackOverflow에 가입하여 이 글을 쓰는 시점(2016년 12월)까지, <a href=\"https://stackoverflow.com/users/123527/simone-carletti\">저는 12만 4천의 명성치를 얻었고</a> 상위 300명의 사용자 랭킹에 올랐습니다.</p>\n<p>가끔씩 다른 프로그래머들이 <em>어떻게 이렇게 활발히 활동할 수 있나요?</em> 혹은 <em>정말 StackOverflow가 좋은건가요 아니면 그냥 눈에 띄고 싶어서 그러는건가요?</em>, <em>하루에 StackOverflow에서 얼마나 시간을 보내시나요?</em> 같은 질문을 하곤 합니다. 저는 이번 포스트를 통해 다양한 질문들을 모아 대답하고 <em>제가 StackOverflow를 어떻게 사용하는지</em> 설명해보고자 합니다.</p>\n<h2 id=\"정말-가치있는-일인가\"><a href=\"#%EC%A0%95%EB%A7%90-%EA%B0%80%EC%B9%98%EC%9E%88%EB%8A%94-%EC%9D%BC%EC%9D%B8%EA%B0%80\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>정말 가치있는 일인가?</h2>\n<p>솔직히 말씀드리겠습니다. (거의) 모든 사람은 가치 없는 일에 매달리지 않습니다(nobody does something for nothing). 우리 모두는 어떤 행동이 가장 중요한지 기대치에 따라 결정을 내립니다.</p>\n<p>하지만 우리 모두 행동의 가치를 매기는 기준이 다릅니다. 어떤 사람은 돈을 받는게 유일한 보상이라고 여기기도 하고, 반대로 어떤 사람은 다른 이들의 눈에 띄거나 명성을 얻는게 돈을 버는 일보다 가치있다고 여깁니다. 특정인들에게 행복이란 단어는 부와 명예와 같은 의미이고, 나머지 사람들에게는 삶의 방식과 직접적으로 연관된 의미입니다.</p>\n<p>이 개념을 프로그래밍의 특정 주제에 적용할 수 있습니다. 바로 오픈 소스입니다. 저는 오픈 소스에 기여하는게 가치 없는 일이고, 그 이유가 <em>돈이 안되어서</em> 라고 말하는 사람들을 많이 보았습니다. 하지만 제게 <a href=\"https://github.com/weppos\">오픈 소스에 기여</a>하는 이유를 물으신다면, 이 일이 저를 더 나은 프로그래머로 만들어주고 제 경력에 긍정적인 도움이 되기 때문이라고 대답할 겁니다. 제가 오늘 직업을 가지고 있는 이유 중 하나가 오픈 소스에 기여하기 때문이며, 뿐만 아니라 여지껏 대단한 사람들과 함께 놀라운 코딩 여정을 함께할 수 있는 기회도 얻었습니다.</p>\n<p>다시 StackOverflow 이야기로 돌아와서, 저는 StackOverflow 안에서 어떤 가치를 발견했기 때문에 참여하기 시작했습니다. 요즘 가입하는 대부분의 신규 사용자들과 달리, 저는 그 때 별다른 도움이 필요치 않았습니다. 사실 가입하고 몇달이 지나서야 처음 질문을 올릴 정도였으니까요.</p>\n<p>2009년에 저는 이미 꽤 쓸만한 개발자였습니다만, 제 활동의 대부분은 이탈리아 커뮤니티에 한정되어 있었습니다. 제가 StackOverflow에서 발견한 것은 바로 세계적인 커뮤니티 안에서 경험과 명성을 얻고, 눈에 띌 수 있는 기회였습니다.</p>\n<p>이렇게 제가 StackOverflow에 가입한 이유를 설명드렸습니다. 하지만 제가 7년이나 계속 답변들을 달게 된 이유는 더 있습니다.</p>\n<h2 id=\"끊임없는-두뇌-훈련\"><a href=\"#%EB%81%8A%EC%9E%84%EC%97%86%EB%8A%94-%EB%91%90%EB%87%8C-%ED%9B%88%EB%A0%A8\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>끊임없는 두뇌 훈련</h2>\n<p>제가 StackOverflow에 엄청난 양의 시간을 투자한 이유 중 단연 첫 번째는 끊임없는 두뇌 훈련을 위해서입니다.</p>\n<p>어떤 사람들은 두뇌 훈련을 위해 십자말풀이를 하거나, <a href=\"https://ko.wikipedia.org/wiki/%EC%8A%A4%EB%8F%84%EC%BF%A0\">스도쿠</a>를 하기도 합니다. 개인적으로 저는 알고리즘, 데이터 마이닝, 프로그래밍 언어와 같이 각기 다른 분야에 걸쳐있는 작은 프로그래밍 문제들을 해결하면서 두뇌 훈련을 하는게 좋습니다.</p>\n<p>그래서 일반적으로 가능한 구현의 변수가 많거나 많은 양의 계층에 걸쳐진 문제같이(예를 들자면 Ruby on Rails와 관련된 질문인데 뷰, 컨트롤러, 데이터베이스의 코드가 다 담겨있는 경우) 큰 규모의 질문은 무시합니다. 대신 작고 잘 정의되어있는 질문은 10분 안에 충분히 답을 할 수 있어서 좋아합니다.</p>\n<h2 id=\"디버깅-기술-향상\"><a href=\"#%EB%94%94%EB%B2%84%EA%B9%85-%EA%B8%B0%EC%88%A0-%ED%96%A5%EC%83%81\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>디버깅 기술 향상</h2>\n<p>적절하게 소프트웨어의 일부를 디버그하는 능력은 소프트웨어 엔지니어에게 아주 가치있는 기술 중 하나입니다.</p>\n<p>만약 자신의 코드를 디버깅하는게 힘들다면, 다른 사람의 코드를 디버깅하는 일은 더욱 힘들겁니다. 게다가 실제로 만날 수도 없는 상태이다보니, 의사소통이 훨씬 더 복잡해집니다. 여러분은 디버깅에 필요한 정보를 빨리 얻기 위해 최대한 적절한 질문을 떠올려야 합니다.</p>\n<p>StackOverflow의 질문들은 '언제나 디버깅할 거리는 무수히 많다'는 점을 보여줍니다. 시간을 들여 답변하기에 적절한 질문을 고르는 기술도 익히게 됩니다.</p>\n<p>때로는 제가 쓰지 않는 라이브러리나 제가 모르는 내용을 디버깅하는 질문에 뛰어들기도 합니다. 이를 통해 저는 <a href=\"https://simonecarletti.com/blog/2009/09/inside-ruby-on-rails-reading-StackOverflowurce-code/\">계속 다른 사람의 코드를 읽는 훈련을 했습니다.</a> 웃긴 이야기를 하나 해 드리겠습니다. 제가 사용하고 있는 특정 라이브러리에 관한 해결책을 찾고 있을 때, 저는 제가 겪었던 문제와 정확히 똑같은 질문을 StackOverflow에서 찾았고 그 질문은 제대로 답변도 달려있었습니다. 별 이상한 점은 없었습니다. 그 답변을 달았던 사람이 저였다는걸 깨닫기 전까지는 말이죠! 비슷한 일이 몇번 있었습니다.</p>\n<h2 id=\"새로운-언어를-배우고-자신의-진도를-확인하기\"><a href=\"#%EC%83%88%EB%A1%9C%EC%9A%B4-%EC%96%B8%EC%96%B4%EB%A5%BC-%EB%B0%B0%EC%9A%B0%EA%B3%A0-%EC%9E%90%EC%8B%A0%EC%9D%98-%EC%A7%84%EB%8F%84%EB%A5%BC-%ED%99%95%EC%9D%B8%ED%95%98%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>새로운 언어를 배우고 자신의 진도를 확인하기</h2>\n<p>저는 새로운 프로그래밍 언어나 기술을 배우기 좋아합니다. 이때는 절대로 StackOverflow를 사용하지 않고, 책이나 온라인 자료에 의지합니다. 하지만 StackOverflow는 자신의 진도를 테스트하는데 아주 좋은 수단이라는 사실을 깨달았습니다.</p>\n<p>불과 몇주 전에 자신이 배우기 시작한 분야에 다른 사람이 질문을 올렸는데, 그 질문에 답변을 할 수 있다면 엄청나게 뿌듯한 기분이 듭니다.</p>\n<p>요즘에는 StackOverflow에 올라온 질문에 답변을 달고, 그 답변이 채택되는 일이 그렇게 간단하지 않습니다. 엄청난 수의 전문가들이 매일 접속해있으니까요. 간단한 질문은 빠르면 몇 분 이내에 답변이 달리기도 하고, 심지어 여러분이 문제를 다 읽기도 전에 답변이 달릴 때도 있습니다. 대신 더 복잡한 질문은 시간이 걸리겠지만, 새로운 주제를 배우고 있는 상태에서 건드리기엔 너무 어렵습니다.</p>\n<p>따라서 적절한 질문을 선택한 뒤, 합리적인 시간 안에 올바른 해결책을 제시하는 일은 새로운 주제를 배우면서 진도를 테스트하는 좋은 방법입니다.</p>\n<p>저는 개인적으로 세 단계의 학습 과정을 가지고 있습니다.</p>\n<ol>\n<li>첫 번째 단계로 열심히 학습하는 동안 간단한 질문들을 읽고 답변을 떠올리지만, <em>실제로는 답변하지 않습니다.</em></li>\n<li>두 번째로 충분한 지식 기반을 쌓았을 때, 좀 쉬운 질문에만 답변을 쓰려고 하거나 이미 알맞은 해결책이 존재한다면 링크를 제공하기도 합니다. <em>제가 정말 이 답이 맞다고 자신있게 생각한다면</em> 직접 답변을 달기도 합니다. 저는 손쉽게 +1을 얻고자 하는게 아니기 때문에 진짜 적절한 해결책이라는 자신이 없다면 답변을 달지 않습니다. 보통 두 번째 단계가 가장 긴 단계인데, 이후 세 번째 단계로 자연스레 이어지게 됩니다.</li>\n<li>세 번째는 해당 주제에 상당한 양의 지식을 갖게 되고, 관련된 대부분의 질문에 대답을 하거나 디버깅할 수 있는 단계입니다. 물론 이 단계에 이르기까지 많은 시간이 들 뿐 아니라, 모든 프로그래밍 관련 주제에서 이 단계까지 다다를 필요는 없을 겁니다.</li>\n</ol>\n<p>일반적으로 두 번째 단계를 목표로 하셔야 합니다. 특정 주제에 관한 질문을 이해하고 괜찮은 수준의 답변을 할 수 있을 정도가 되어야 합니다. 비록 대부분의 경우 여러분이 올바른 해결책을 떠올리기까지 걸리는 시간이 다른 숙련된 개발자들에 비해 느리다고 하더라도 괜찮습니다. 답변을 다는 시간을 줄이는 능력은 더 많은 경험에서 우러나오기 마련인데, 반드시 시간 줄이기를 목표로 삼을 필요는 없습니다.</p>\n<h2 id=\"사용자-지원\"><a href=\"#%EC%82%AC%EC%9A%A9%EC%9E%90-%EC%A7%80%EC%9B%90\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>사용자 지원</h2>\n<p>가끔 누군가가 <a href=\"https://dnsimple.com/\">DNSimple(작성자가 몸 담고 있는 서비스)</a>과 관련된 질문을 올릴 때도 있습니다. 대부분의 질문은 자신의 소프트웨어를 연결하거나 도메인을 등록하는 등 DNSimple이 하나의 구성요소인 경우가 많습니다.</p>\n<p>질문에 이미 제시된 답변이 없는 경우 저는 가능한 한 대화에 끼어들어 답을 제공하려 합니다. 만약 필요한 지식을 모르고 있다면 다른 DNSimple 팀원을 불러 그 사람과 함께 알맞은 해결책을 작성해봅니다.</p>\n<p>저는 StackOverflow에서 <a href=\"https://stackoverflow.com/questions/tagged/dnsimple\">dnsimple</a> 태그를 구독해두었습니다. 그리고 때로는 <a href=\"https://serverfault.com/\">ServerFault</a>와 <a href=\"https://webmasters.stackexchange.com/\">Webmasters Stack Exchange</a>에서 <code>dnsimple</code> 키워드를 찾아보기도 합니다.</p>\n<h2 id=\"온라인-평판\"><a href=\"#%EC%98%A8%EB%9D%BC%EC%9D%B8-%ED%8F%89%ED%8C%90\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>온라인 평판</h2>\n<p>작년에 저는 <em>온라인 평판</em>을 쌓는 것이 중요하다는 이야기를 많이 들었습니다. 이게 SEM(Search Engine Marketing) 회사들이 다른 회사들더러 온라인 활동에 투자하도록 꼬드기는 가장 일반적인 방법 중 하나입니다.</p>\n<p>온라인 평판은 마찬가지로 사람에게도 중요합니다. 특히 여러분의 주 활동 영역이 어떻게든 컴퓨터나 프로그래밍, 혹은 인터넷이랑 직접적인 연관이 있는 분야라면 말입니다.</p>\n<p>StackOverflow는 <a href=\"https://en.wikipedia.org/wiki/Show,_don&#x27;t_tell\"><em>말하지 말고 행동으로 보이라</em></a> 는 접근 방식으로 온라인 평판을 쌓는 아주 좋은 방법입니다. StackOverflow에 있는 질문에 적절히 답을 하면서 여러분은 자신의 경험을 내보일 수 있습니다. 경험이 있는 척 하거나 다른 사람에게 무엇을 잘 한다고 말만 하고 다니는 것보다 훨씬 직접적입니다. 물론 다른 결과가 나올 수도 있습니다. 질문과 답변은 공개되어있고 몇 년 뒤에도 지워지지 않기 때문에, 자신의 기술 혹은 소통 능력의 약점을 드러낼 수 있기 때문입니다.</p>\n<p>예전부터 저는 최소한 두 가지 유형의 StackOverflow 프로필을 가지고 있는 사람들과 이야기해왔습니다. 첫 번째는 자신들의 온라인 평판에 긍정적인 영향을 미치는 방식으로 소통을 해온 사람들이고, 두 번째는 자신이 <em>너무 멍청하다고</em> 자책하며 질문을 올리는 사람들이었습니다.</p>\n<p>기억하세요. 바보같은 질문은 없습니다. 아무리 질문이 쉽다 하더라도 정말 중요한 것은 질문하는 방식(그리고 여러분이 적절한 답을 찾아보거나 최소한 노력이라도 했는지) 입니다. 저는 특정 분야에 제가 지식이 부족하다는 이야기를 하면서 질문하는데 아무런 거리낌이 없습니다. 제가 <a href=\"https://dba.stackexchange.com/\">DBA</a>나 <a href=\"https://vi.stackexchange.com/\">Vim</a> 사이트에 올린 질문들은 대부분 제 자신이 해당 분야의 전문가가 아니지만, 더 배우고 발전하고자 하는 의지를 보이고 있습니다.</p>\n<h2 id=\"온라인-노출도\"><a href=\"#%EC%98%A8%EB%9D%BC%EC%9D%B8-%EB%85%B8%EC%B6%9C%EB%8F%84\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>온라인 노출도</h2>\n<p>온라인 평판이 여러분의 프로필을 발전시키는데 직접적으로 연관되어 있다면, 온라인 노출도는 여러분이 온라인에서 보여지는 횟수를 뜻합니다.</p>\n<p>Github는 프로그래머에게 온라인 평판(인터뷰에 지원할 때 자신의 오픈 소스 기여를 링크로 제공할 수 있기 때문에)과 온라인 노출도를 기르는 최적의 장소입니다. 하지만 Github와 비교하면 StackOverflow는 일반적으로 검색 엔진에서 많이 보이게 됩니다. 저는 여지껏 검색 엔진을 이용해서 StackOverflow에 있는 문제와 해결책을 찾는 데 어려움을 겪었다는 프로그래머를 본 적이 없습니다.</p>\n<p>StackOverflow는 검색 엔진에서 높은 우선순위를 가지고 있기 때문에, StackOverflow에서 명성을 얻는 일은 아마 여러분의 온라인 노출도에 직접적, 간접적으로 영향을 주게 됩니다. 직접적으로는 StackOverflow의 랭킹이 올라가고, 간접적으로 여러분이 답변을 달았던 질문이 검색 엔진의 결과 페이지에 나타나기 때문입니다.</p>\n<h2 id=\"영어-배움터\"><a href=\"#%EC%98%81%EC%96%B4-%EB%B0%B0%EC%9B%80%ED%84%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>영어 배움터</h2>\n<p>저는 영어 원어민이 아닙니다. 영어로 글을 쓰는 일은 저에게 여전히 어렵고, 이탈리아어로 쓰는 일보다 훨씬 어려우며, 다른 사람이 영어로 쓴 글을 읽는 일보다 더욱 어렵습니다.</p>\n<p>StackOverflow는 제 영어 쓰기 실력을 기르는데 아주 좋은 기회를 제공해줍니다. 누구나 제 답변을 수정하거나 오타를 교정할 수 있기 때문입니다. StackOverflow는 모든 변경 사항의 이력을 가지고 보존하기 떄문에, 저는 언제나 제 글에 다른 사람들이 도움을 준 부분을 살펴볼 수 있습니다. 실수로부터 배우는 일은 저에게 아주 좋은 방법입니다.</p>\n<h2 id=\"결론\"><a href=\"#%EA%B2%B0%EB%A1%A0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>결론</h2>\n<p>StackOverflow에 있는 질문에 답변을 작성하는 것은 <em>일반적으로</em> 저에게 기쁜 경험입니다. 저의 태도와 개인적인 역량에 긍정적인 영향을 주기 때문입니다. 하지만 긍정적인 경험이 짜증나는 일이 되도록 하지 않기 위해 이 일을 의무로 여기지 않고 있습니다.</p>\n<p>저는 최소 몇시간 혹은 최소 몇일에 한번 답변을 해야한다거나, 하루에 얼마만큼의 명성을 쌓아야 한다고 목표치를 정해놓지 않았습니다. 마지막으로 로그인한 시간이 좀 지났다 하더라도 찝찝한 기분이 들지도 않습니다.</p>\n<p>저는 하루 일과와 StackOverflow에 머물러 있는 시간의 균형을 유지하려고 노력합니다. 주로 테스트나 배포가 끝나길 기다리는 동안 하루에 30분에서 한 시간정도만 머물러 있습니다.</p>\n<p>StackOverflow에 기여하는 일은 두뇌를 단련하고, 소프트웨어 엔지니어로서 자신의 기술을 끌어올리며, 저의 온라인 평판과 노출도를 항샹시키는 긍정적인 부가 효과도 있습니다. 이 모든 이유가 제가 7년 동안 질문에 답하기 위해 <em>투자한</em> 시간을 정당화합니다.</p>\n<hr>\n<h2 id=\"번역-후기\"><a href=\"#%EB%B2%88%EC%97%AD-%ED%9B%84%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>번역 후기</h2>\n<p>글 내용이 꽤 길어서 힘들었지만 유익한 내용이었습니다. 저를 비롯한 많은 개발자들이 StackOverflow에서 이미 답변된 질문 위주로 검색을 하고, 답을 얻어서 문제를 해결할 생각은 하겠지만 직접 질문을 올리거나 답변을 작성할 생각은 별로 해 본적이 없을겁니다. 영어라는 장벽 때문이겠지요. 거기다 '바빠 죽겠는데 이런데 글 쓰고 앉아있을 시간이 어디 있겠느냐' 같은 생각도 할 수 있겠네요.</p>\n<p>이 글을 읽고 나니 StackOverflow를 적극적으로 사용하는 일은 Github으로 참여하는 오픈 소스 생태계와 비슷한 점이 많다고 느꼈습니다. 보통은 자신의 코드가 직접적으로 남는 Github쪽을 선호하겠지만, 오히려 무리해서 '<a href=\"https://blog.outsider.ne.kr/1193\">일일 커밋</a>'을 하기보단 차근차근 StackOverflow에 적극적으로 참여하는 것도 개발자의 실력을 갈고닦는데 큰 도움이 되겠습니다.</p>\n<p>그리고 다른 어느 부분보다 글의 결론 부분이 와 닿았습니다. 우리는 보통 각자의 생업이 있고, 아무리 좋은 의도를 가진 행동도 의무가 되어야 할 필요는 없다고 생각합니다. 스타워즈의 요다 선생이 <strong>\"Do. Or do not. There is no try.\"</strong> 라고 말씀하셨지만, 이번 경우에는 정 반대로 생각해보면 마음이 한결 편해집니다. <strong>\"아예 안하는 것 보다는 시도라도 해 보는게 낫지 않을까\"</strong></p>","id":"/Users/rinae/Dev/blog/src/pages/posts/2017-04-15-how-i-use-stackoverflow-kor/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-04-15","path":"/posts/how-i-use-stackoverflow-kor","title":"[번역] 내가 StackOverflow를 사용하는 방법","tags":["Learning","Translation"],"category":"Translation"}},{"excerpt":"이 포스트는  Alex Kotliarskyi 의  글 을 번역한 것입니다. 오역 지적 및 피드백은 언제나 환영합니다. 저는 드디어  Cheng Lou 가 ReactConf'17에서 발표한 내용을 이해했습니다. 20분정도 시간을 내실 수 있다면 이 영상을 먼저 봐 주세요. 요약:  언어가 있으면  메타언어 (고차 언어)도 있다. 여기서 언어는 실제 소스 코드이고, 메타 언어는 그 위에 있는 모든 것(테스트, 주석, IDE…","html":"<ul>\n<li><strong>이 포스트는 <a href=\"http://frantic.im/\">Alex Kotliarskyi</a>의 <a href=\"http://frantic.im/meta-language\">글</a>을 번역한 것입니다.</strong></li>\n<li><strong>오역 지적 및 피드백은 언제나 환영합니다.</strong></li>\n</ul>\n<hr>\n<p>저는 드디어 <a href=\"https://twitter.com/_chenglou\">Cheng Lou</a>가 ReactConf'17에서 발표한 내용을 이해했습니다. 20분정도 시간을 내실 수 있다면 이 영상을 먼저 봐 주세요.</p>\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/_0T5OSSzxms\" frameborder=\"0\" allowfullscreen></iframe>\n<p><strong>요약:</strong> 언어가 있으면 <a href=\"https://ko.wikipedia.org/wiki/%EB%A9%94%ED%83%80_%EC%96%B8%EC%96%B4\">메타언어</a>(고차 언어)도 있다. 여기서 언어는 실제 소스 코드이고, 메타 언어는 그 위에 있는 모든 것(테스트, 주석, IDE 기능, 문서, 예제, 튜토리얼, 블로그 글, 책, 비디오, 컨퍼런스 등의 기술 발표 등)을 뜻한다. 우리는 언어를 배우기 위해 메타언어를 사용한다. 우리가 메타언어의 개념을 언어 자체로 끌어내리면, 우리가 다루는 개념은 더욱 유연해지고 점차 새로운 메타언어를 만들어나갈 수 있게 된다. 새로운 메타언어는 같은 에너지를 쏟아도 기존 메타언어에 비해 더 고차원적이고 풍부한 형태를 가질 수 있게 된다.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 790px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 49.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The Flow\"\n        title=\"\"\n        src=\"/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-ffc88.png\"\n        srcset=\"/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-d16f8.png 198w,\n/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-f21b1.png 395w,\n/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-ffc88.png 790w,\n/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-81e7e.png 1185w,\n/static/2017-04-04-1b52eac1f90484ea982f2dbab689b235-c6d11.png 1200w\"\n        sizes=\"(max-width: 790px) 100vw, 790px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>이 그림의 이해를 돕기 위해 다양한 예제를 준비해왔습니다.</p>\n<h2 id=\"어셈블리-명령어-→-변수명\"><a href=\"#%EC%96%B4%EC%85%88%EB%B8%94%EB%A6%AC-%EB%AA%85%EB%A0%B9%EC%96%B4-%E2%86%92-%EB%B3%80%EC%88%98%EB%AA%85\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>어셈블리 명령어 → 변수명</h2>\n<p>우주 산업 분야에서 행성으로부터의 <a href=\"https://ko.wikipedia.org/wiki/%ED%83%88%EC%B6%9C_%EC%86%8D%EB%8F%84\">탈출 속도(escape velocity)</a>를 구하기 위해 사용되는 코드 일부를 살펴보겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-assembly\"><code>.SUB_0_21:\n    ; Computes escape velocity\n    ; register xmm0 - planet mass\n    ; retister xmm1 - planet radius\n    mulsd   xmm0, qword ptr [rip + .LCPI0_0]\n    divsd   xmm0, xmm1\n    xorps   xmm1, xmm1\n    sqrtsd  xmm1, xmm0\n    ucomisd xmm1, xmm1\n    jp      .LBB0_2\n    movapd  xmm0, xmm1\n    ret\n.LBB0_2:\n    jmp     sqrt                    ; TAILCALL\n.LCPI0_0:\n    .quad   4459223850755291651     ; double 1.334816E-10</code></pre>\n      </div>\n<p>코드의 상당수가 메타언어에 기대고 있습니다. 이 코드에 대한 주석이나 문서(여기엔 보이지 않지만) 말입니다. 위 코드와 비교해서 C로 작성된 예를 살펴보겠습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-c\"><code><span class=\"token macro property\">#<span class=\"token directive keyword\">define</span> GRAVITY_CONSTANT 6.67408e-11</span>\n<span class=\"token keyword\">double</span> <span class=\"token function\">escape_velocity</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">double</span> mass<span class=\"token punctuation\">,</span> <span class=\"token keyword\">double</span> radius<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token function\">sqrt</span><span class=\"token punctuation\">(</span><span class=\"token number\">2</span> <span class=\"token operator\">*</span> GRAVITY_CONSTANT <span class=\"token operator\">*</span> mass <span class=\"token operator\">/</span> radius<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>그저 변수에 이름을 지정해주었을 뿐인데 메타언어에 기대고 있던 정보를 언어 자체로 끌어내릴 수 있게 됐습니다.</p>\n<h2 id=\"변수명-→-심화-타입advanced-types\"><a href=\"#%EB%B3%80%EC%88%98%EB%AA%85-%E2%86%92-%EC%8B%AC%ED%99%94-%ED%83%80%EC%9E%85advanced-types\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>변수명 → 심화 타입(Advanced Types)</h2>\n<p>C 버전의 코드는 어셈블리로 작성된 코드보다 훨씬 많은 정보를 가지고 있고, 메타언어도 더 높은 수준으로 이루어져 있습니다. 이제 우리는 반복 작성을 하거나 코드 안의 내용이 무엇인지 문서화할 필요가 없어졌습니다. 이 함수가 2개의 인자를 받아야 하며 첫 번째 인자는 질량, 두 번째 인자는 반경이라는 내용 말입니다.</p>\n<p>하지만 아직 문맥상 코드에 부족한 부분이 있습니다. 이 함수가 어느 단위 위에서 동작하는지 알 수가 없다는 겁니다. 당장은 라이브러리의 문서(메타언어)에 기댈 수 밖에 없습니다.</p>\n<p>우리는 인자 이름을 <code>mass_kilograms</code>, <code>radius_meters</code> 같은 이름으로 바꾸는 간단한 트릭을 써 볼수 있지만, 언어 자체에서 '이 함수는 반드시 킬로그램과 미터를 입력해야한다'고 보장해주는 장치가 없습니다.</p>\n<p>이제 더 복잡한 타입을 지원하는 언어의 예를 살펴보겠습니다. 저는 OCaml을 사용하겠지만 대부분의 ML-계열 언어들(하스켈, 스위프트 등)은 이런 기능을 지원합니다. (<a href=\"https://docs.microsoft.com/en-us/dotnet/articles/fsharp/language-reference/units-of-measure\">그리고 F#은 아예 단위에 대해 가장 높은 우선 순위로 지원을 하고 있습니다.</a>)</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ocaml\"><code><span class=\"token keyword\">let</span> gravity<span class=\"token punctuation\">_</span>constant <span class=\"token operator\">=</span> <span class=\"token number\">6.67408e-11</span>\n\n<span class=\"token keyword\">type</span> mass <span class=\"token operator\">=</span> Kilograms <span class=\"token keyword\">of</span> float\n<span class=\"token keyword\">type</span> length <span class=\"token operator\">=</span> Meters <span class=\"token keyword\">of</span> float\n<span class=\"token keyword\">type</span> velocity <span class=\"token operator\">=</span> MetersPerSecond <span class=\"token keyword\">of</span> float\n\n<span class=\"token keyword\">let</span> escape<span class=\"token punctuation\">_</span>velocity <span class=\"token punctuation\">(</span>Kilograms mass<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">(</span>Meters radius<span class=\"token punctuation\">)</span> <span class=\"token operator\">=</span>\n    MetersPerSecond <span class=\"token punctuation\">(</span><span class=\"token number\">2.0</span> <span class=\"token operator\">*.</span> gravity<span class=\"token punctuation\">_</span>constant <span class=\"token operator\">*.</span> mass <span class=\"token operator\">/.</span> radius <span class=\"token operator\">|></span> sqrt<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p><em>실은 지구로부터의 탈출 속도를 계산할 때 제 구현체에서 버그를 발견했습니다. 질량을 엉뚱한 단위로 입력하고 있었어요.</em></p>\n<p>위의 코드에서 <code>length</code> 와 <code>mass</code> 는 단순한 숫자가 아니라 1급 객체(first class citizen)입니다. 덕분에 더욱 안전하고 유지보수가 용이하며 코드를 발전시키기 쉬워졌지요. 예를 들어 우리가 야드파운드법을 지원하고자 한다면(왜 그래야하는지는 모르겠지만), <code>mass</code> 타입을 이렇게 바꾸면 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ocaml\"><code><span class=\"token keyword\">type</span> mass <span class=\"token operator\">=</span>\n  <span class=\"token operator\">|</span> Kilograms <span class=\"token keyword\">of</span> float\n  <span class=\"token operator\">|</span> Pounds <span class=\"token keyword\">of</span> float\n</code></pre>\n      </div>\n<p>이러면 타입 체커가 기존 코드에서 새 단위를 적용하기 위해 수정해야 할 부분을 낱낱이 지적해줄 겁니다.</p>\n<h2 id=\"사용되지-않을-경우deprecation와-다른-메타정보들\"><a href=\"#%EC%82%AC%EC%9A%A9%EB%90%98%EC%A7%80-%EC%95%8A%EC%9D%84-%EA%B2%BD%EC%9A%B0deprecation%EC%99%80-%EB%8B%A4%EB%A5%B8-%EB%A9%94%ED%83%80%EC%A0%95%EB%B3%B4%EB%93%A4\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>사용되지 않을 경우(Deprecation)와 다른 메타정보들</h2>\n<p>우리가 기존의 탈출 속도보다 훨씬 정확한 값을 구할 수 있는 새로운 공식을 발견했다고 칩시다. 그렇다면 기존의 함수가 더 이상 사용되지 않는다고 표시하려면 어떻게 해야할까요?</p>\n<p>이럴 때 보통 주석이나 문서를 추가하거나 Medium에 글을 쓰는 등 메타언어를 구축할 것입니다. 하지만 메타언어로 구성된 정보들을 코드 자체로 끌어내리는게 훨씬 더 생산적인 방법입니다.</p>\n<p>전에 썼던 트릭처럼 C 함수의 이름을 바꾸어 <code>escape_velocity</code> 를 <code>escape_velocity_DEPRECATED</code> 나 <a href=\"https://github.com/facebook/react/blob/80bff5397bf854750dbe7c286f61654ea58938c5/src/umd/ReactUMDEntry.js#L21\">이렇게 괴상한 이름으로 바꿀 수도 있습니다</a>. 하지만 이는 우리의 API가 변경되면서 클라이언트의 코드가 동작하지 않게 되고, 표준적인 방법도 아닙니다(누구나 자신만의 접미사를 붙일 수 있으니까요). 그리고 새로이 사용해야 할 메서드를 제대로 추천하지 않고 있습니다.</p>\n<p>만약 언어 자체가 이 메타정보를 알아서 알려주는 기능을 지원한다면 어떨까요?</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ocaml\"><code><span class=\"token keyword\">let</span> escape<span class=\"token punctuation\">_</span>velocity <span class=\"token operator\">=</span> <span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>\n<span class=\"token punctuation\">[</span><span class=\"token operator\">@</span>ocaml<span class=\"token punctuation\">.</span>deprecated <span class=\"token string\">\"Please use `quantum_escape` instead\"</span><span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<h2 id=\"추가-예시\"><a href=\"#%EC%B6%94%EA%B0%80-%EC%98%88%EC%8B%9C\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>추가 예시</h2>\n<p>지금까지 메타적 공간에 있던 많은 정보를 언어 자체에 심어주는 하나의 방법을 보여드렸습니다. 다른 예들을 보여드리겠습니다.</p>\n<p><strong><a href=\"https://ko.wikipedia.org/wiki/%EB%B9%84%EC%A7%80%ED%84%B0_%ED%8C%A8%ED%84%B4\">방문자 패턴(Visitor Pattern)</a>과 <code>for</code> 루프 → map/filter/reduce:</strong> 제가 대학에 다닐 땐 방문자 패턴을 배웠지만, 이젠 이 패턴이 많은 언어에서 기본 라이브러리로 손쉽게 제공되고 있습니다.</p>\n<p><strong>상태(state) 불러오기:</strong> 저는 종종 사람들이 리액트 컴포넌트를 만들면서 상태를 이렇게 설정해두는 것을 보게 됩니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>state <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  data<span class=\"token punctuation\">:</span> <span class=\"token keyword\">null</span><span class=\"token punctuation\">,</span>\n  error<span class=\"token punctuation\">:</span> <span class=\"token keyword\">null</span><span class=\"token punctuation\">,</span>\n  loading<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>이 경우 <code>loading</code> 이 <code>false</code> 라면 <code>data</code> 나 <code>error</code> 중에 하나가 <code>null</code> 이 되어서는 안된다는 안내를 하려면 메타언어를 사용할 수 밖에 없습니다. 그 대신 우리는 Flow나 타입스크립트(Typescript)를 사용하여 언어 단위에서 제약을 걸 수 있습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-typescript\"><code><span class=\"token keyword\">type</span> State <span class=\"token operator\">=</span>\n  <span class=\"token punctuation\">{</span> progress<span class=\"token punctuation\">:</span> <span class=\"token string\">'loading'</span> <span class=\"token punctuation\">}</span> <span class=\"token operator\">|</span>\n  <span class=\"token punctuation\">{</span> progress<span class=\"token punctuation\">:</span> <span class=\"token string\">'done'</span><span class=\"token punctuation\">,</span> data<span class=\"token punctuation\">:</span> Object <span class=\"token punctuation\">}</span> <span class=\"token operator\">|</span>\n  <span class=\"token punctuation\">{</span> progress<span class=\"token punctuation\">:</span> <span class=\"token string\">'error'</span><span class=\"token punctuation\">,</span> error<span class=\"token punctuation\">:</span> Error <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><strong>리덕스(Redux)와 불변성(Immutability):</strong> 현재는 문서, 블로그 포스트, 비디오, 컨퍼런스 발표 등에서 리덕스를 사용할 때 변수나 객체에 직접적으로 변화를 주지 않을 것을(lack of direct mutation) 전제하고 있습니다. 만약 자바스크립트가 기본적으로 불변 데이터 타입과 타입 선언(type annotations)을 제공한다면 위에서 말하는 전제를 코드에서 바로 표현할 수 있지 않을까요?</p>\n<p><strong>프로미스(Promises)와 옵저버블(Observables):</strong> 자바스크립트에서 프로미스 패턴은 라이브러리에서 제공하는 기능이었지만 이제 코어 기능으로 자리잡았습니다. <a href=\"https://github.com/tc39/proposal-observable\">옵저버블</a>에도 같은 과정이 진행중입니다.</p>\n<h2 id=\"결론\"><a href=\"#%EA%B2%B0%EB%A1%A0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>결론</h2>\n<p>아무리 메타언어를 언어로 끌어내린다 하더라도 사람 사이에 의사소통하는 부분을 해결할 수는 없다는 것을 새겨두시는게 좋습니다. 하지만 언어의 진화와 함께 우리는 더 효과적인 의사소통의 기회를 열어가고 있습니다.</p>\n<p>반드시 어딘가엔 복잡한 부분이 존재합니다. 만약 언어가 아주 단순하다면, 복잡한 부분은 메타언어에 머무르게 됩니다.</p>\n<p>참고로  <a href=\"https://www.youtube.com/watch?v=4anAwXYqLG8\">Sebastian Markbåge의 최소한의 API 표현 범위(minimal API surface area)에 관한 발표와</a>, <a href=\"https://www.youtube.com/watch?v=V1po0BT7kac\">Jared Forsyth의 타입 시스템에 대한 발표</a>를 살펴보세요. 아 그리고 영화 <a href=\"http://movie.daum.net/moviedb/main?movieId=105570\">컨택트(Arrival, 2016)</a>도 꼭 보세요. 강추합니다!</p>\n<hr>\n<h2 id=\"번역-후기\"><a href=\"#%EB%B2%88%EC%97%AD-%ED%9B%84%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>번역 후기</h2>\n<p>처음 이 글을 접하게 된 계기는 이 코드 때문이었습니다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token comment\">// explicit</span>\n<span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>obj <span class=\"token operator\">!==</span> undefined<span class=\"token punctuation\">)</span> <span class=\"token keyword\">return</span> obj<span class=\"token punctuation\">.</span>x<span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\">// implicit</span>\n<span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>obj<span class=\"token punctuation\">)</span> <span class=\"token keyword\">return</span> obj<span class=\"token punctuation\">.</span>x<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>보고 계시는 자바스크립트 코드에서 위쪽에 제시된 코드가 평균적으로 최대 15%까지 처리 속도가 빠르다고 합니다. <a href=\"https://twitter.com/dhh/status/846627223176167424\">DHH는 트위터에서</a> '어차피 15%라고 해봤자 실제로는 몇 밀리초 이하일텐데 왜 기계에게 맞추려고 하느냐' 같은 내용의 비판을 쏟아내었지요. 코드를 어떻게 작성하실건지는 여러분의 취향에 맡기겠습니다.</p>\n<p>하지만 오늘 제가 드리고픈 말씀은 아래 트윗에 다 있기 때문에 후기를 짧게 줄이고자 합니다.</p>\n<blockquote class=\"twitter-tweet\" data-conversation=\"none\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\"><a href=\"https://twitter.com/dhh\">@dhh</a> Code is not a conversation between a dev and a machine.&#13;&#13;Code is a conversation between a developer AND THE NEXT DEVELOPER.</p>&mdash; Don Schenck (@DonSchenck) <a href=\"https://twitter.com/DonSchenck/status/846675893909180416\">March 28, 2017</a></blockquote>\n<script async src=\"//platform.twitter.com/widgets.js\" charset=\"utf-8\"></script>","id":"/Users/rinae/Dev/blog/src/pages/posts/2017-04-04-understanding-taming-the-meta-language-kor/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-04-04","path":"/posts/understanding-taming-the-meta-language-kor","title":"[번역] 메타언어 길들이기","tags":["Programming","Learning","Translation"],"category":"Translation"}},{"excerpt":"본 포스트는  Gerald Weinberg 의  포스팅 의 번역본입니다 피드백은 언제나 환영합니다 제가 처음 이 질문을 Quora에서 보았을 때, 이미 4…","html":"<ul>\n<li><strong>본 포스트는 <a href=\"https://en.wikipedia.org/wiki/Gerald_Weinberg\">Gerald Weinberg</a>의 <a href=\"http://secretsofconsulting.blogspot.kr/2017/02/should-i-learn-c-or-python.html\">포스팅</a>의 번역본입니다</strong></li>\n<li><strong>피드백은 언제나 환영합니다</strong></li>\n</ul>\n<hr>\n<p>제가 처음 이 질문을 Quora에서 보았을 때, 이미 47개 정도의 답변이 달려있었고 대부분의 답은 잘못되어 있었습니다. 하지만 이 많은 답변들은 여러분에게 무언가 시사하는 바가 있습니다. 프로그래밍 언어를 선택하는 것은 기술적이라기보단 종교적인 질문에 가깝다는 겁니다. 사실 당신이 전문적인 프로그래머가 되고자 한다면 두 언어를 동시에 배워야 합니다.</p>\n<p>우리가 프로그래밍을 가르칠 때는 언제나 두 언어를 동시에 가르칩니다. 과제는 반드시 두 가지(혹은 그 이상의) 언어로 작성되어야 하며, 왜 언어마다 해결법이 다른지, 혹은 왜 같은지 설명하는 에세이를 같이 첨부하도록 합니다. 이 방법으로 여러분의 전문적 코딩 지식과 성숙도를 발전시킬 수 있습니다.</p>\n<p>일부 답변자들은 프로그래밍 언어는 도구에 불과하다고 주장합니다. 만약 그 비유가 맞다면, 목수가 되고자 하는 사람들이 이런 질문을 할 때 어떻게 대답해야 할까요?</p>\n<blockquote>\n<p>\"제가 톱질하는 법을 배워야 할까요? 아니면 나사돌리개 쓰는 법을 배워야 할까요?\"</p>\n</blockquote>\n<p>둘 중에 하나만 배운 사람이 최고 수준의 목수가 될 수 있을까요?</p>\n<p>그러니 이 결론이 날 리 없고 유사종교스러운 논란을 멀리하는 대신에, 여러분의 소중한 시간을 가능한한 많은 프로그래밍 언어를 배우는데 쓰도록 하십시오. 최소한 다섯 개에서 여섯 개 정도를 배우세요. 모든 언어를 반드시 사용할 필요는 없지만, 언어별로 다른 접근방식을 알게 되면 이런 바보같은 말은 하지 않게 될 것입니다: </p>\n<blockquote>\n<p>\"저는 X라는 언어만 알고 있어요, 그리고 여전히 이 언어가 세상에서 가장 뛰어난 언어라고 생각합니다.\"</p>\n</blockquote>\n<h2 id=\"번역-후기\"><a href=\"#%EB%B2%88%EC%97%AD-%ED%9B%84%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>번역 후기</h2>\n<p>요즘은 시간이 너무나 빨리 지나가버려서 1주일에 한 개 정도 번역을 하겠다는 다짐을 지키지 못하게 되는 때가 잦습니다. 다시 번역에 시동을 걸면서 이번에는 짧지만 의미있는 글을 번역해보았습니다.</p>\n<p>우연히 이 글을 보게 되었을 때 '이 나이 지긋한 어르신은 누구신가' 하고 찾아보니 명망있는 IT 컨설턴트시더군요. 병환으로 사경을 헤메신 적이 있다고 하는데 아직까지는 잘 계십니다. 최근 트윗을 보니 병원에 다시 투병중이신가 봅니다. 쾌차하시길 바랍니다.</p>\n<p>'프로그래밍 언어는 도구에 불과하다' 라는 말을 다른 분에게 들었을 때는 이 글에서 이야기하고자 하는 뉘앙스와 약간 달랐습니다. '개발에 필요한 원리를 숙지하고, 실무에서 필요한(혹은 가장 빠르게 적용 가능한) 언어를 익혀서 금방금방 적용할 수 있어야 한다.' 라는 뜻으로 말씀을 하셨었지요. 어느 쪽이든, 결론적으로 다양한 언어를 접하는 것은 프로그래머로서 사고의 폭을 넓히기 때문에 도움이 된다는 뜻이라고 생각합니다.</p>\n<p>취지는 좋지만 현실적인 문제를 생각해보면.. 시간이 부족하다는 장벽이 있겠지요. 이럴 때는 조급한 마음가짐을 잠시 내려놓고 배움의 호흡을 길게 가져가야 할 것 같습니다. 제가 이 글의 번역을 하겠다고 허락을 구하기 위해 메일을 보낼 때 주고받았던 메일 일부를 발췌해 보았습니다.</p>\n<blockquote>\n<p>Rinae: (중략) 저는 루비와 자바스크립트라도 동시에 익혀나가고 있습니다. 하지만 쉽지는 않네요.</p>\n<p>Gerald: 음, 그 두개면, 충분히 제가 추천한 방식대로 하고 있군요. 앞서 언급했듯이 같은 일을 양쪽 언어로 구현해보세요. 그렇다고 무조건 구체적으로 작성할 필요도 없지만, 최소한 서로 다른 두 언어가 어떻게 다른지 이해하고 최소한의 스케치라도 디자인할 수 있을 정도가 되어야 합니다.</p>\n<p>그리고 이렇게 하는 게 아주 어렵다고 느껴진다면 최소한 앞의 두 언어가 익숙해질 때 까지는 세 번째 언어를 배우지 않도록 하세요.</p>\n</blockquote>\n<blockquote>\n<p>Rinae: 새로 배울 언어를 추천해주실 수 있나요?</p>\n<p>Gerald: 기존에 알고 있던 언어와 가능한한 다른 언어를 고르세요. 다른 언어를 고르기 위해 인터넷에서 찾아보거나 프로그래머 지인들에게 그 사람들이 알고 있는 언어를 물어보세요. 그 대신에 지금 알고 있는 언어와 너무 비슷한 언어를 고르게 된다면 헷갈리기만 할 겁니다<em>(역주: 언어의 문법이 다른 정도만 헷갈리고 나머지는 별 의미 없을 것이라는 의미로 보입니다)</em>.</p>\n</blockquote>\n<p>이 글의 저자는 이후에 다른 글 <a href=\"http://secretsofconsulting.blogspot.kr/2017/02/how-long-can-i-remain-ruby-java-c.html\">'How Long Can I Remain a [Ruby, Java, C++, Python, …]  Programmer?'</a> 를 작성하여 더 깊게 생각할 거리를 던져주었습니다. 이 글의 후속편이라고 할 수 있겠지만, 굳이 번역하지는 않을거라 한번 읽어보시면 좋겠습니다. 저 글의 한줄 요약을 남겨드리자면 이렇습니다.</p>\n<blockquote>\n<p>\"특정 언어의 프로그래머로 몇년 먹고살 수 있을지 전전긍긍하지 말고 ‘Programmer’ or ‘Problem-solver’ 같은 사람이 되어 살아남을 생각을 하라''</p>\n</blockquote>","id":"/Users/rinae/Dev/blog/src/pages/posts/2017-03-11-should-i-learn-c++-or-python-kor/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-03-11","path":"/posts/should-i-learn-cpp-or-python-kor","title":"[번역] C++와 Python 중 무엇을 먼저 배울까?","tags":["Learning","Translation"],"category":"Translation"}},{"excerpt":"이 포스트는  Ian C. Anderson…","html":"<ul>\n<li><strong>이 포스트는 <a href=\"https://twitter.com/IanCAnderson\">Ian C. Anderson</a>의 <a href=\"https://robots.thoughtbot.com/beginners-mind\">글</a>을 번역한 것입니다</strong></li>\n<li><strong>번역에 관한 감상 및 피드백은 작성자에게 큰 도움이 됩니다</strong></li>\n</ul>\n<hr>\n<p>저는 루비 프로그래머입니다. 저는 이 언어를 즐겨 쓰며, 직업적으로 소프트웨어를 만들면서 루비가 저의 호기심과 흥미를 충족시켜준다고 믿고 있습니다. 저는 매일 끊임없이 루비와 레일즈에 대해 배우고 있지만 이 언어와 플랫폼에 대해서는 잘 이해하고 있습니다.</p>\n<p>그러다가 최근에 하스켈을 배우기 시작했습니다. <a href=\"https://thoughtbot.com/\">Thoughtbot</a>의 하스켈 프로젝트에 기여하고 싶었고, 제 시야를 확장해보고 싶었기 때문입니다.</p>\n<p>하스켈은 근본적으로 루비와 다릅니다. 순수한 함수형 언어이며 엄격한 정적 타입을 요구합니다. 루비가 동적 타입을 가지고 있고 함수형과는 거리가 먼 객체지향 언어라는 것과는 다릅니다. 제가 함수형 언어를 다루는 수준은 아주 미약합니다. 그래서 함수형 언어의 여러 개념과 하스켈이란 언어 자체가 저에게는 생소합니다.</p>\n<p>현 시점에서 하스켈 세계는 신비롭고 경이롭게 느껴집니다. 그래서 그런지 배우고자 하는 사람들에게는 동기부여와 흥분되는 기분을 가져다 줄 겁니다.</p>\n<h2 id=\"무한한-가능성\"><a href=\"#%EB%AC%B4%ED%95%9C%ED%95%9C-%EA%B0%80%EB%8A%A5%EC%84%B1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>무한한 가능성</h2>\n<blockquote>\n<p>초심자의 마음속에는 많은 가능성이 자리잡고 있다, 그러나 전문가들은 그 가능성이 적다.(In the beginner’s mind there are many possibilities, but in the expert’s there are few.)</p>\n<p>— <em>Shunryu Suzuki, <a href=\"https://www.amazon.com/Zen-Mind-Beginners-Shunryu-Suzuki/dp/1590308492\">Zen Mind, Beginner's Mind.</a></em></p>\n</blockquote>\n<p>이 인용문은 당신이 공부하고 있는 주제에 익숙해질 수록 점점 좁은 시야를 가지게 되는 것을 경계하고 있습니다. 저의 경우엔 문제 해결을 위한 만능 도구로서 루비를 갈고 닦으면서도, 언제나 제 기술적 선택에 만족하지 않도록 신경씁니다. 언제든지 더 나은 방법이 있다고 <em>믿으며</em> 그 방법을 찾아야 합니다.</p>\n<h2 id=\"전문가의-모순\"><a href=\"#%EC%A0%84%EB%AC%B8%EA%B0%80%EC%9D%98-%EB%AA%A8%EC%88%9C\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>전문가의 모순</h2>\n<blockquote>\n<p>모순되게도 마치 좁은 시야와 선입견을 가지게 되는 것처럼, 아주 탄탄하게 쌓아올린 전문 지식은 퍼포먼스의 저하를 초래할 수 있다.(Paradoxically, the very underpinning of expertise can entail degradation in performance as well, such as tunnel vision and biases.)</p>\n<p>— <em>Itiel E. Dror, <a href=\"https://core.ac.uk/download/pdf/1717017.pdf\">The paradox of human expertise: Why experts get it wrong</a></em></p>\n</blockquote>\n<p>지식이 늘어날 수록 무엇이 어떻게 동작하는 지에 대해 추측하는 일도 늘어납니다. 이런 선입견들은 전문가로서 효율적으로 정보를 처리하고 문제를 푸는데 능숙하게 만들어 줄 수도 있습니다. 그러나 \"기본적인(defaults)\" 해답은 전문가들에게 주어진 문제를 해결하기 위한 더 적절한 답을 고민하지 못하게 만들 수 있습니다. 전문가들의 뇌는 특정 분야에 특화되어 있습니다. 말하자면 좁고 엄격한 마음가짐을 가지게 된다는 겁니다.</p>\n<h2 id=\"정신적-기민함의-가치\"><a href=\"#%EC%A0%95%EC%8B%A0%EC%A0%81-%EA%B8%B0%EB%AF%BC%ED%95%A8%EC%9D%98-%EA%B0%80%EC%B9%98\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>정신적 기민함의 가치</h2>\n<blockquote>\n<p>지능의 척도는 변화할 수 있는 능력이다.(The measure of intelligence is the ability to change.)</p>\n<p>— <em>Albert Einstein</em></p>\n</blockquote>\n<p>저는 thoughbot의 <a href=\"https://thoughtbot.com/playbook#research\">Research Trello board</a>가 생각났습니다. 우리는 이 장소를 우리가 진행하는 일이나 기술 스택의 변화를 실험하는 장소로 사용합니다. 우리는 어떤 툴을 사용하고 어떻게 사용하는지에 대해 고집스럽게 굴지만<em>(역주: 예를 들면 Thoughtbot의 개발자들은 대부분 Vim을 사용한다고 합니다)</em>, 언젠가 더 좋은 도구가 있다면 기꺼이 갈아탈 용의가 있습니다.  과학적 방법을 통해 지속적으로 우리 자신의 가설에 도전하는 것은, 우리가 주어진 문제를 해결하기 위해 현재와 미래에 걸쳐 최선의 도구와 기술을 사용하고 있다는 확신을 들게 합니다. 기술 분야에서 변화는 피할수 없습니다. 그러므로 이런 정신적 기민함은 굉장히 가치있는 자산입니다.</p>\n<h2 id=\"당신의-지혜-주머니를-키워라\"><a href=\"#%EB%8B%B9%EC%8B%A0%EC%9D%98-%EC%A7%80%ED%98%9C-%EC%A3%BC%EB%A8%B8%EB%8B%88%EB%A5%BC-%ED%82%A4%EC%9B%8C%EB%9D%BC\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>당신의 지혜 주머니를 키워라</h2>\n<blockquote>\n<p>… 몇몇 경우에 지혜롭게 모나드의 힘을 적용하면 우리는 중첩된 콜백을 풀어낼 수 있고, 코드의 일부분을 재사용가능하게 만들며, 전반적으로 우리의 프로그램을 더 좋게 만듭니다.</p>\n<p>— Tom Stuart, <a href=\"http://codon.com/refactoring-ruby-with-monads\">Refactoring Ruby with Monads</a></p>\n</blockquote>\n<p><a href=\"https://en.wikipedia.org/wiki/Monad_(functional_programming)\">모나드</a>는 함수형 프로그래밍의 추상 개념인데 하스켈에서 필수적입니다. <a href=\"http://codon.com/refactoring-ruby-with-monads\">Tom의 발표</a>에서 모나드를 이용하여 이상한 루비 코드 덩어리들을 멋진 해법으로 바꾸는 세 가지 실전적인 예시를 볼 수 있습니다. Tom의 코드가 그가 경험한 함수형 프로그래밍의 도움을 받았다는 것은 자명합니다. 그는 평상시의 문제를 해결하기 위해 다른 개념을 빌려올 수 있다는 것을 보여주었습니다. 이상적인 루비 코딩 스타일을 유지하면서요.</p>\n<p>제가 하스켈 전문가가 될 수 있을까요? 아니면 제가 하스켈 프로젝트를 풀 타임으로 다루게 될까요? <a href=\"https://robots.thoughtbot.com/if-you-gaze-into-nil-nil-gazes-also-into-you\">아마도요</a>. 만약 아니라고 해도 하스켈을 배운 경험이 무의미하다고 생각하진 않을 겁니다. 저는 제 지혜 주머니를 키우고 있고(I am adding to my bag of tricks), 그래서 다각도로 문제를 해결해볼 수 있습니다. 제가 어떤 레일즈 앱의 기능을 구현할 때 함수형 프로그래밍의 접근 방식이 저의 평소 객체지향적 코드보다 더 나을지 생각해 볼 것입니다.</p>\n<p>전문가가 되기 위해 분투하는 것은 가치있는 일입니다. 그 비결은 끊임없이 특정 경우에 더 나을 수도 있는 대안을 찾아가면서, 더욱 많은 지식과 경험을 흡수하는 것입니다.</p>\n<hr>\n<h2 id=\"번역-후기\"><a href=\"#%EB%B2%88%EC%97%AD-%ED%9B%84%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>번역 후기</h2>\n<p>저도 루비 프로그래머입니다(쑥스럽지만 원본 글 첫머리를 빌려서 한마디 적어봅니다). 매일 새로운 것을 1이라도 알면 그 뒤에 알아야 할 것들이 10이 넘는 하루하루를 보내고 있습니다. 하지만 나름 재미있지요. 당장 저의 주력 언어라고 말 할 수 있는 루비는 아직 잘 다룬다고 생각하지 않아서 더 깊게, 깊게 파고들어야 한다고 생각하고 있었습니다.</p>\n<p>물론 이 글에서 말하고자 하는 주제는 이미 알고 있었습니다만 언제나 '아직은 다른 언어나 개념을 접하기엔 좀 부족해..' 라는 생각을 하면서 고개를 돌리지 않고 있습니다. 올해 안에는 적어도 Elixir나 Scala에 도전해보고 싶긴 합니다. 물론 루비 이외에 다른 도전은 언제나 하고 있지요. 바로 자바스크립트입니다.</p>\n<p>저에게는 너무나 변화가 빠른 언어이고, 겉으로 보기에는 별거 없어 보이지만 다루고자 하면 굉장히 복잡하고(+골때리고) 코드를 잘 짜기 위한 고민을 계속 해도 잘 되지 않는 언어입니다. 지금은 이 두 언어를 번갈아 익히는 것 만으로도 벅차네요.</p>\n<p>제가 본격적으로 제 자신을 '개발자' 라고 지칭할만한 일을 하게 된지 이제 1년이 되었습니다. 이 시점에서 이번 글은 다시 한번 끊임없는 향상심에 대해 환기시켜주는 글이었습니다. 다음 해에는 어떤 글을 남길 수 있을지 제 자신에게 조금 더 기대를 걸어봅니다.</p>","id":"/Users/rinae/Dev/blog/src/pages/posts/2017-02-20-beginners-mind-kor/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-02-20","path":"/posts/beginners-mind-kor","title":"[번역] 초심자의 마음가짐","tags":["Learning","Translation"],"category":"Translation"}},{"excerpt":"이 글은  Joël Quenneville 의  글 을 번역한 것입니다 번역 및 내용에 관한 피드백은 언제나 환영합니다 수 년에 걸쳐 저는  Elm 을 배워왔습니다. 보통 저는 새로운 언어를 배울 때 이러저러한 과정을 거칩니다: 튜토리얼을 따라가본다 많은 토이 프로젝트를 만든다 블로그 글을 읽는다 해당 언어의 주간 뉴스레터를 구독한다 그런데 기대하지 않던 순간에 제 고정관념을 바꾸는 일이 생겼습니다. 저는  elm-lang…","html":"<ul>\n<li>이 글은 <a href=\"https://twitter.com/joelquen\">Joël Quenneville</a>의 <a href=\"https://robots.thoughtbot.com/learning-by-helping\">글</a>을 번역한 것입니다</li>\n<li>번역 및 내용에 관한 피드백은 언제나 환영합니다</li>\n</ul>\n<hr>\n<p>수 년에 걸쳐 저는 <a href=\"http://elm-lang.org\">Elm</a>을 배워왔습니다. 보통 저는 새로운 언어를 배울 때 이러저러한 과정을 거칩니다:</p>\n<ul>\n<li>튜토리얼을 따라가본다</li>\n<li>많은 토이 프로젝트를 만든다</li>\n<li>블로그 글을 읽는다</li>\n<li>해당 언어의 주간 뉴스레터를 구독한다</li>\n</ul>\n<p>그런데 기대하지 않던 순간에 제 고정관념을 바꾸는 일이 생겼습니다.</p>\n<p>저는 <a href=\"https://elmlang.herokuapp.com\">elm-lang 슬랙 커뮤니티</a>에 가입하여 <code>#beginners</code> 채널을 둘러보기 시작했습니다. 대부분의 질문이 \"X를 위한 문법이 뭐죠?\" 이거나 \"Y를 하려면 어떻게 하죠?\" 같은 질문이었습니다. 대부분은 공식 문서에 답이 적혀있는 질문들이었습니다.</p>\n<p>소프트웨어 산업에서 짬이 찬 대부분의 사람들과 같이, 저는 몇가지 기술을 연마해왔습니다:</p>\n<ul>\n<li>구글을 뒤적인다</li>\n<li>문서를 읽는다</li>\n<li>가설을 테스트하기 위한 작은 실험을 디자인한다</li>\n</ul>\n<p>![]({{ site.baseurl }}/assets/2017-02-13-sc1.png)</p>\n<p>사람들에게 그냥 \"제발 그놈의 메뉴얼을 읽어보세요(<a href=\"https://ko.wikipedia.org/wiki/RTFM\">RTFM</a>)\", \"구글은 여러분의 친구입니다\" 혹은 \"REPL(콘솔)에서 한번 쳐보세요\" 같은 말을 하는게 편할 수도 있습니다. 그러나 이는 공감하기 힘들거나 자각하기 힘든 답변이 됩니다. 여러분이 당연하게 여기는 기술들은 다른 사람에게는 그렇지 않습니다. 특히 <code>#beginners</code>  채널 같은곳에서 질문을 하는 사람들에게는요.</p>\n<p>그 대신에 저는 문서를 찾아서 해당 질문에 대해 적절한 답이 될 수 있는 부분의 링크를 공유해 주었습니다. 필요하다면 설명도 곁들였습니다. 그러면서 단순히 문법이나 함수에 대해 찾아주는 것을 넘어 더 개념적인 질문을 돕기 시작 했습니다:</p>\n<blockquote>\n<p>왜 컴파일러는 이벤트 핸들러를 위한 스니펫으로 타입 <code>Html a</code> 를 쓰는 것을 좋아하지 않을까요?</p>\n</blockquote>\n<blockquote>\n<p><code>&#x3C;select></code> 에다 이벤트를 어떻게 집어넣을 수 있나요?</p>\n</blockquote>\n<blockquote>\n<p><code>type User = User String Int</code> 같은 중복은 어떻게 처리되나요?</p>\n</blockquote>\n<p>결과적으로 저는 빠르게 Elm의 문법과 코어 라이브러리에 관한 지식을 다질 수 있었습니다. 그리고 최근에 생겨난 <a href=\"https://robots.thoughtbot.com/elms-universal-pattern\">패턴</a>에 대해 살펴보기 시작했습니다. 제가 전혀 몰랐던 라이브러리에 관해 도울 수도 있게 되었습니다.</p>\n<p>간단하게 <a href=\"https://ko.wikipedia.org/wiki/%ED%8D%BC%EC%A7%95\">퍼즈 테스터</a>를 만들려면 어떻게 해야할까요? <code>map</code> 작동에 대해서 알아야겠군요. JSON 디코더 3개를 엮으려 하신다구요? 아마 <code>Json.Decode.map3</code> 함수가 있을걸요?</p>\n<p>점점 이런 작업이 편해지면서, 저는 단순히 문서만 살펴보는게 아니라 소스를 파고들어 왜 특정 행동이 특정 방법으로 작동하는지 설명하고자 하였습니다.</p>\n<p>시간이 흘러, 자주 질문이 들어온다고 느끼는 주제에 대해서 블로그 글도 작성했습니다. 이를테면 <a href=\"https://robots.thoughtbot.com/building-custom-dom-event-handlers-in-elm\">'커스텀 DOM 이벤트 핸들러를 작성하는 방법'</a>처럼요.</p>\n<p>이러한 과정을 거치는 동안 <a href=\"https://robots.thoughtbot.com/beginners-mind\">초심자의 마음가짐</a>을 유지하는 것은 중요합니다. 때때로, 질문에 대답하는 것은 함께 배워나가는 과정이었습니다. 저는 처음에는 답을 몰랐거나 아주 모호하게 이해하고 있었을 뿐이었지만 토론을 거쳐가면서 지식을 발전시킬 수 있었습니다. 다른 때엔 제가 대답할수 없었지만 지금은 알게 된 개념에 대해 설명하려고 노력했습니다. 겸손함과 '잘 모르겠습니다' 라고 말하는 능력은 질문 받는 상황(these situations)을 배움의 기회로 바꿀 수 있습니다.</p>\n<p>다른사람에게 조언하거나 도움을 주기 위해 \"전문가\"가 될 때까지 기다리지 마세요. 당신이 초보자라 하더라도 그 결과는 엄청납니다(The payoffs are massive). 끊임없이 배우고, 끊임없이 지식을 나누세요.</p>\n<hr>\n<h2 id=\"번역-후기\"><a href=\"#%EB%B2%88%EC%97%AD-%ED%9B%84%EA%B8%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>번역 후기</h2>\n<p>사실 저는 Elm을 들어는 봤지만 제대로 사용해본 적은 없습니다. 함수형 프로그래밍의 개념에 대해서 가볍게 알아보고자 할 때 이러한게 있다 까지는 보았고, 호기심에 가벼운 튜토리얼을 따라가보았지만 아직 저에겐 너무 어려웠습니다.</p>\n<p>하지만 이 글의 내용은 Elm이랑 크게 관련 있는 것은 아니고, 개발자로서 끊임없이 배움의 과정을 걷는 사람들에게 생각할 거리를 준다는 느낌을 받아 번역을 해 보았습니다. 그래서 위의 Elm관련 번역은 부적절한 번역일 수 있으니 Elm을 사용해본 분들의 많은 조언 부탁드립니다.</p>\n<p>저 자신도 RORLAB 슬랙의 <code>#question_answer</code> 채널에 질문이 올라오면 당장 제가 모르는 내용이라도 검색을 해 봅니다. 제가  답변했던 내용들 일부는 제가 실제로 해 본 것도 아니고, 구글에서 적절하게 검색하여 찾은 내용이지만 해당 질문을 한 회원은 문제를 해결할 수 있었습니다. 그러면서 제가 몰랐던 지식도 조금씩 알아나갈 수 있었고 개발 문서만 보는 것을 넘어서 소스를 들여다 보는데 거부감이 많이 줄어들게 되었습니다.</p>\n<p>이 글의 핵심은 마지막 문장이라 생각합니다. 저도 당연히 '번역의 전문가'가 아님에도 이러저러한 글을 번역해서 올리고 있습니다. 분명 더 예전에 번역했던 글들의 퀄리티를 생각하면 다시 하고 싶지만..시간상.. 넘어가도록 하고, 조금씩 찾아가고 물어가면서 퀄리티를 향상시키고자 하고 있습니다. 제가 작성하는 코드도 그렇게 되었으면 하는 바람입니다.</p>\n<p>다음에는 <a href=\"https://robots.thoughtbot.com/beginners-mind\">'초심가의 마음가짐'</a>에 대한 글을 번역해보려 합니다.</p>","id":"/Users/rinae/Dev/blog/src/pages/posts/2017-02-13-learning-by-helping-kor/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-02-13","path":"/posts/learning-by-helping-kor","title":"[번역] 도우면서 배우기","tags":["Learning","Translation"],"category":"Translation"}}],"tagName":"Learning"}}

/***/ })

});


